.main-page {
    height: 100vh;
    width: 100vw;
}

.main-header {
    width: 100vw;
    height: (100vh / 8);
    border: 2px solid black;
    background-size: cover;
    background-image: url(/img/first_page/tomato-2780424_1920_2_Easy-Resize.com.jpg);
    display: flex;
    justify-content: center;
    align-items: center;

    h2 {
        font-size: 16vw;
        color: $color-title;
        font-family: $font-main;
    }
}

%btn-category {
    width: 100vw;
    height: (100vh / 8);
    border: 2px solid black;
    background-size: cover;

    a {
        width: 100vw;
        height: (100vh / 8);
        display: flex;
        align-items: center;
        text-decoration: none;
        position: relative;

        h3 {
            font-size: 8vw;
            color: black;
            position: absolute;
        }
    }
}

div[class$="category"] {
    @extend %btn-category;
}

.snack-category {
    background-image: $snack-header-img;
    background-position: 0% 30%;

    h3 {
        left: 55%;
    }
}

.salad-category {
    background-image: $salad-header-img;
    background-position: 50% 30%;
    transform: scale(-1, 1);

    h3 {
        transform: scale(-1, 1);
        right: 10%;
    }
}

.meat-category {
    background-image: $meat-header-img;
    background-position: 40% 100%;

    h3 {
        left: 47%;
    }
}

.vege-category {
    background-image: $vege-header-img;

    h3 {
        left: 10%;
    }
}

.soup-category {
    background-image: $soup-header-img;
    background-position: 0% 4%;

    h3 {
        left: 70%;
    }
}

.sweet-category {
    background-image: $sweet-header-img;
    background-position: 0% 45%;

    h3 {
        left: 10%;
    }
}

.smoothie-category {
    background-image: $smoothie-header-img; 
    background-position: 0% 15%;
    transform: scale(-1, 1);

    h3 {
        transform: scale(-1, 1);
        right: 62%;
    }
}